<template>
  <!-- 表单 -->
  <div>
    <el-row>
      <el-col :offset="2" :span="10">
        <el-row>
          <!-- 日期筛选 -->
          <dropFilter @triggerFilter="closeMonth" >
            日期筛选
            <template v-slot:checkgroup>
              <el-checkbox-group class="check-group" v-model="searchMonth">
                <el-checkbox
                  v-for="(item, index) in monthList"
                  :key="index"
                  :label="item.value"
                >{{ item.text }}</el-checkbox>
              </el-checkbox-group>
            </template>
          </dropFilter>

          <!-- 分类筛选 -->
          <dropFilter @triggerFilter="closeCate" class="search-by-cate">
            筛选分类
            <template v-slot:checkgroup>
              <el-checkbox-group v-model="searchCategory">
                <el-checkbox
                  v-for="(item, index) in categoryList"
                  :key="index"
                  :label="item.id"
                >{{ item.name }}</el-checkbox>
              </el-checkbox-group>
            </template>
          </dropFilter>

          <!-- 消除所有筛选 -->
          <el-button type="danger" @click="clearFilter">清除所有筛选</el-button>

          <!-- 新增分类 -->
          <add-cate-form :categoryList="categoryList" class="add-form" />

          <!-- 新增账单 -->
          <add-form
            class="add-cate-form"
            :updateDataList.sync="filterList"
            :dataList="dataList"
            :monthList="monthList"
            :categoryList="categoryList"
          />
        </el-row>

        <!-- 表格 -->
        <transition name="el-fade-in">
          <el-table v-if="isRender" :data="filterList" :row-style="test">
            <el-table-column width="60" prop="typeName" label="类型" />
            <el-table-column width="250" prop="time" label="日期" />
            <el-table-column sortable prop="detail" label="分类" />
            <el-table-column sortable prop="amount" label="金额" />
          </el-table>
        </transition>
      </el-col>

      <!-- 支出总结 -->
      <el-col :offset="1" :span="4">
        <transition name="el-fade-in">
          <outcome-form
            v-if="isRender"
            :outcomeList="classification(0)"
            :categoryList="categoryList.filter((e) => e.type === 0)"
          />
        </transition>
      </el-col>

      <!-- 收入总计 -->
      <el-col :offset="1" :span="4">
        <transition name="el-fade-in">
          <income-form
            v-if="isRender"
            :incomeList="classification(1)"
            :categoryList="categoryList.filter((e) => e.type === 1)"
          />
        </transition>
      </el-col>
    </el-row>
  </div>
</template>

<script>
// 支出表格
import outcomeForm from "./_outcome_From";
// 收入表格
import incomeForm from "./_intcome_Form";
// 新增账单
import addForm from "./_add_Form";
// 新增分类
import addCateForm from "./_add_cate_Form";
// 二次封装element下拉组件
import dropFilter from "./_dropfilter";
export default {
  async created() {
    try {
      const { billList, categoryList } = await this.$axios.get(
        "api/get_DataList"
      );
      this.filterList = this.dataList = billList;
      this.categoryList = categoryList;
    } catch (error) {}

    this.isRender = true;
    // 月份
    for (let i = 1; i < 13; i++) {
      this.monthList.push({
        text: i + "月",
        value: i
      });
    }
  },
  data() {
    return {
      isRender: false,
      // 表单数据
      dataList: [],
      // 筛选后数据表单
      filterList: [],
      //分类信息
      categoryList: [],
      //按月份进行筛选
      monthList: [],
      // 月份筛选条件
      searchMonth: [],
      // 分类筛选条件
      searchCategory: []
    };
  },
  methods: {
    // 支出分类
    classification(type) {
      let detailSum = [];

      this.categoryList.map(item => {
        let amontObj = {
          name: item.name,
          amount: 0
        };

        // 找出同类项目并累加
        this.filterList
          .filter(e => e.category === item.id)
          .filter(e => parseInt(e.type) === type)
          .map(e => (amontObj.amount += parseInt(e.amount)));

        detailSum.push(amontObj);
      });

      // 去除金额为0项目
      detailSum = detailSum.filter(e => e.amount !== 0);

      return detailSum;
    },
    // 月份筛选栏隐藏时自动进行筛选
    closeMonth(isClose) {
      if (!isClose) this.searchByMonth();
    },
    // 分类筛选栏隐藏时默认进行筛选
    closeCate(isClose) {
      if (!isClose) this.searchByCategory();
    },
    // 按月份搜索
    searchByMonth() {
      if (!this.searchMonth.length) {
        !this.searchCategory.length
          ? (this.filterList = this.dataList)
          : this.searchByCategory();
        return;
      }

      const newFilter = [];
      // 先判断是否有经过分类筛选，如果有，则直接对filter数组进行二次筛选即可
      if (!!this.searchCategory.length) {
        this.searchMonth.map(month => {
          const filterMon = this.filterList.filter(
            e => new Date(e.time).getMonth() + 1 === month
          );
          newFilter.push(...filterMon);
        });
      } else {
        this.searchMonth.map(month => {
          const filterMon = this.dataList.filter(
            e => new Date(e.time).getMonth() + 1 === month
          );
          newFilter.push(...filterMon);
        });
      }
      this.filterList = newFilter;
    },
    // 按分类搜索
    searchByCategory() {
      if (this.searchCategory.length === 0) {
        !this.searchMonth.length
          ? (this.filterList = this.dataList)
          : this.searchByMonth();
        return;
      }

      const newFilter = [];
      if (this.searchMonth.length !== 0) {
        this.searchCategory.map(cat => {
          const filterCate = this.filterList.filter(e => e.category === cat);
          newFilter.push(...filterCate);
        });
      } else {
        this.searchCategory.map(cat => {
          const filterCate = this.dataList.filter(e => e.category === cat);
          newFilter.push(...filterCate);
        });
      }
      this.filterList = newFilter;
    },
    // 清除所有筛选条件
    clearFilter() {
      this.searchMonth = [];
      this.searchCategory = [];
      this.filterList = this.dataList;
    },
    // 支出底色为红，收入底色为绿
    test({ row, rowIndex }) {
      const { type } = row;
      return type === 0
        ? { backgroundColor: "rgba(255,100,97,0.2)" }
        : { backgroundColor: "	rgb(154,255,154,0.2)" };
    }
  },
  components: {
    outcomeForm,
    incomeForm,
    addForm,
    addCateForm,
    dropFilter
  }
};
</script>

<style>
.add-form {
  float: right;
}
.add-cate-form {
  float: right;
  margin-right: 1%;
}
.search-by-cate {
  margin: 0 1%;
}
</style>
