<template>
  <div>
    <!-- 新增分类  -->
    <el-button type="primary" class="add-category" @click="show = true">新增分类</el-button>
    <el-dialog title="新增账单" :visible.sync="show" width="30%">
      <el-form :model="submitList" ref="form" label-width="80px">
        <el-form-item label="类型" prop="type">
          <el-select v-model.number="submitList.type" placeholder>
            <el-option label="收入" :value="1" />
            <el-option label="支出" :value="0" />
          </el-select>
        </el-form-item>

        <el-form-item label="名称" prop="name">
          <el-input v-model.number="submitList.name" />
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button @click="add_cate" type="primary">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    categoryList: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      show: false,
      submitList: {
        name: "",
        type: ""
      }
    };
  },
  methods: {
    async add_cate() {
      try {
        const res = await this.$axios.post(
          "/api/add_category",
          this.submitList
        );
        this.categoryList.push(res);
        this.resetForm();
      } catch (error) {}
    },
    resetForm() {
      this.show = false;
      this.$refs.form.resetFields();
      for (let i in this.submitList) {
        this.submitList[i] = "";
      }
    }
  }
};
</script>

