<template>
<!-- 支出总计 -->
  <div>
    <h2>总支出</h2>
    <h3>￥{{spendSum}}</h3>
    <el-table  :data="outcomeList" stripe>
      <el-table-column prop="name" label="分类" />
      <el-table-column sortable prop="amount" label="总计(￥)" />
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    outcomeList: {
      type: Array,
      default: () => [],
    },
    categoryList: {
      type: Array,
      default: () => [],
    },
  },
  computed: {
    // 计算总支出
    spendSum() {
      let Sum = 0;
      this.outcomeList.map(e => Sum += e.amount);
      return Sum; 
    }
  },
};
</script>

