<template>
  <div>
    <el-button type="success" @click="show = true">新增账单</el-button>
    <el-dialog title="新增账单" :visible.sync="show" width="30%" @close="resetForm">
      <el-form :rules="rules" :model="addForm" ref="form" label-width="80px">
        <el-form-item label="类型" prop="type">
          <el-select  v-model.number="addForm.type" placeholder>
            <el-option label="收入" :value="1" />
            <el-option label="支出" :value="0" />
          </el-select>
        </el-form-item>

        <el-form-item label="分类" prop="category">
          <el-select v-model="addForm.category" :disabled="staticCateList.length === 0" placeholder="先选择类型">
            <el-option v-for="item in staticCateList" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>

        <el-form-item label="金额" prop="amount">
          <el-input v-model.number="addForm.amount" />
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="show = false">取 消</el-button>
        <el-button @click="add" type="primary">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    categoryList: {
      type: Array,
      default: () => [],
    },
    dataList: {
      type: Array,
      default: () => [],
    },
    monthList: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      show: false,
      addForm: {
        category: "",
        amount: "",
        type: "",
      },
      rules: {
        amount: [{ required: true, message: "金额不能为空!", trigger: "change" }],
        category: [{ required: true, message: "分类不能为空!", trigger: "change" }],
        type: [{ required: true, message: "类型不能为空！", trigger: "blur" }],
      }
    };
  },
  computed: {
    staticCateList() {
      const { type } = this.addForm;
      return this.categoryList.filter(e => parseInt(e.type) === type ); 
    }
  },
  methods: {
    add() {
      this.$refs["form"].validate(async (valid) => {
        if (valid) {
          const { category } = this.addForm;

          this.addForm["detail"] = this.categoryList.find((e) => e.id === category).name;
          // 账单创建时间
          this.addForm["time"] = new Date().getTime();

          try {
            const res = await this.$axios.post("api/add_data", this.addForm);

            this.dataList.unshift(res);

            this.resetForm();
          } catch (error) {}
        } else {
          return false;
        }
      });
    },
    resetForm() {
      this.show = false;
      this.$refs.form.resetFields();
      for (let i in this.addForm) {
        this.addForm[i] = "";
      }
    }
  },
};
</script>

