<template>
  <!-- 收入总计 -->
  <div>
    <h2>总收入</h2><h3>￥{{incomeSum}}</h3>
    <el-table :data="incomeList" stripe>
      <el-table-column prop="name" label="分类" />
      <el-table-column sortable prop="amount" label="总计(￥)" />
    </el-table>
  </div>
</template>

<script>
export default {
  props: {
    incomeList: {
      type: Array,
      default: () => []
    },
    categoryList: {
      type: Array,
      default: () => []
    }
  },
  computed:{
    // 计算总收入
    incomeSum(){
      let Sum = 0;
      this.incomeList.map(e => Sum += e.amount);
      return Sum;
    }
  }
};
</script>

